import { useState } from "react";

import { NavLink, Link } from "react-router-dom";
import {
    AiFillCheckCircle,
    AiFillDingtalkCircle,
    AiFillDropboxCircle,
    AiFillGitlab,
    AiFillSketchCircle,
    AiFillMediumCircle,
    AiFillHeart,
    AiFillInsurance,
    AiFillCaretDown,
    AiFillCaretUp,
} from "react-icons/ai";

import classes from "./Sidebar.module.css";

const Sidebar = () => {
    const [moreProducts, setMoreProducts] = useState(false);

    const toggleProductsHandler = () => {
        setMoreProducts((prevState) => !prevState);
    };

    return (
        <div className={`${classes.sidebar} w-60 bg-gray-800 text-white p-4 flex flex-col gap-2`}>
            <h3 className="text-center uppercase bg-red-300">
                <Link to="#products">Products</Link>
            </h3>
            <ul className="" id="">
                <li className="flex items-center gap-2">
                    <AiFillCheckCircle />
                    <NavLink className="hover:text-pink-700" to="AmericanCake">
                        Bnh to M
                    </NavLink>
                </li>
                <li className="flex items-center gap-2">
                    <AiFillDingtalkCircle />
                    <NavLink className="hover:text-pink-700" to="AmericanCake">
                        Bnh Mochi Nht Bn
                    </NavLink>
                </li>
                <li className="flex items-center gap-2">
                    <AiFillDropboxCircle />
                    <NavLink className="hover:text-pink-700" to="AmericanCake">
                        Bnh Tiramisu 
                    </NavLink>
                </li>
                {moreProducts && (
                    <>
                        <li className="flex items-center gap-2">
                            <AiFillGitlab />
                            <NavLink className="hover:text-pink-700" to="AmericanCake">
                                Bnh Macaron Php.
                            </NavLink>
                        </li>
                        <li className="flex items-center gap-2">
                            <AiFillHeart />
                            <NavLink className="hover:text-pink-700" to="AmericanCake">
                                Bnh Tapioca Brazil.
                            </NavLink>
                        </li>
                        <li className="flex items-center gap-2">
                            <AiFillInsurance />
                            <NavLink className="hover:text-pink-700" to="AmericanCake">
                                Bnh Sachertorte o.
                            </NavLink>
                        </li>
                        <li className="flex items-center gap-2">
                            <AiFillMediumCircle />
                            <NavLink className="hover:text-pink-700" to="AmericanCake">
                                Bnh Pavlova c.
                            </NavLink>
                        </li>
                        <li className="flex items-center gap-2">
                            <AiFillSketchCircle />
                            <NavLink className="hover:text-pink-700" to="AmericanCake">
                                Bnh Black Forest c.
                            </NavLink>
                        </li>
                    </>
                )}
            </ul>
            <button className="flex items-center gap-2 justify-center" onClick={toggleProductsHandler}>
                {moreProducts ? "Less Products..." : "More Products..."}
                {moreProducts ? <AiFillCaretUp /> : <AiFillCaretDown />}
            </button>

            <h3 className="text-center uppercase p-0 bg-">Profiles</h3>
            <div className="profiles flex flex-col">
                <Link to='#updateAvatar'>Update avatar</Link>
                <Link to='#updateEmail'>Update email</Link>
                <Link to='#updateUsername'>Update username</Link>
                <Link to='#ChangePassword'>Change password</Link>
            </div>
        </div>
    );
};

export default Sidebar;
